.pagination__container {
    display: flex;
    width:300px;
    margin: auto;
    justify-content: space-around;
}
.pagination__button {
    background: darkcyan;
    width:75px;
    text-align: center;
    color: aliceblue;
    padding: 5px;
    margin-bottom: 50px;
    cursor: pointer;
    display: block;
    outline: none;
    border: none;
}
.pagination__button:disabled {
    background: lightgray;
    color:lightslategray;
}
.pagination__text {
    line-height: 30px;
}