.header {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width:100%;
    height:85px;
    background: #373737;
    justify-content: space-around;
    column-gap: 150px;
}
.header a {
    text-decoration: none;
}
.logo__img {
    width:70px;
    height:70px;
    margin-right:3%;
}
.logo__text {
    margin:0;
    padding: 0;
    color:#FFFFFF;
    min-width: 100%;
    line-height: 60px;
}
.header__logo {
    display: flex;
}
.header__input {
    height: 2em;
    width:35%;
    background-color: #808080;
    border: none;
    border-radius: 4px;
    color: #FFFFFF;
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
    padding-left:5%;
    cursor: pointer;  
}

.header__input::placeholder {
    background-image: url('https://i.imgur.com/wMa4WUe.png');
    background-size: 14px;
    background-position:0% 50%;
    background-repeat: no-repeat;
    color: #FFFFFF;
    position: relative;
    padding-left:5%;
}
.header__input:focus::placeholder {
    color: transparent;
    background: none;
}
.header__input:focus {
    background-image: none;
    outline: none;
}
.about {
    text-decoration: none;
    color:#FFFFFF
}