.reviews {
    width:40%;
    margin: auto;
}
.reviews__header {
    text-align: center;
}
.reviews__item {
    border-radius: 15px;
    padding: 15px;
    margin-bottom: 35px;
    border-bottom: 1px solid grey;
}
.item__picture {
    max-width:500px;
    max-height: 500px;
    margin-top: 50px;
}
.item__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}
.reviews__item_positive {
    background: lightgreen;
}
.reviews__item_negative {
    background: lightcoral;
}