.item {
    width:250px;
    animation: appear 0.8s;
}
.item__img {
    width:250px;
    height: 250px;
    display: block;
    margin: 15px auto 15px;
}
.item__text {
    text-align: center;
}
.item__header {
    text-align: center;
}
.item__button {
    color: #FFF;
    background: #373737;
    border: none;
    padding:10px;
    cursor: pointer;
    display: block;
    margin:20px auto;
}
@keyframes appear {
    from { 
      transform: translateY(150%);
      opacity: 0.2;
    }
  }