.modal__container {
    position: fixed;
    top:0;
    left: 0;
    width: 100vw;
    height: 100vh;
    cursor: pointer;
    background:rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
}
.modal__content {
    width:300px;
    height: 300px;
    background: white;
    border-radius: 30px;
    cursor: default;
    padding: 15px;
}
.content__header {
    text-align: center;
}
.content__text {
    margin-bottom: 5px;
}
.content__button {
    display: block;
    margin: auto;
    color: #FFF;
    background: #373737;
    border: none;
    padding:10px;
    cursor: pointer;
    outline: none;
    margin-top: 20px;
    width:80px;
}
.content__button a {
    text-decoration: none;
    color: #FFF;
}
.modal__close {
    width:30px;
    height:30px;
    line-height: 30px;
    text-align: center;
    background: white;
    border-radius: 20px;
    top:0;
    right: 0;
}
.modal__wrapper {
    display: flex;
    width:350px;
}